#score {
  text-align: left;
  margin-left: 20px;
  font-size: 55px;
}
#score.correct {
  color: green;
}
#score.incorrect {
  color: black;
}
#sentence {
  /* margin-top: var(--margin-top); remove all this */
  line-height: normal;
  user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -webkit-touch-callout: none;
  -webkit-text-size-adjust: none;
  touch-action: manipulation; /* blocks double tap zoom */
  font-family: helvetica;
}
#sentence.completed {
  transition: margin-top 700ms ease-in-out;
  margin-top: 20px; /* will have to change */
}
.word {
  display: inline-block;
  position: relative;
  transition: transform 700ms ease-in-out;
}
.correct {
  /* font-style: normal; */
  color: green;
}
.incorrect {
  /* font-style: italic; */
  color: black;
}
.wordSelected {
  text-decoration: underline dotted #00000050;
  -webkit-text-decoration: underline dotted #00000070;
}
.wordSwap {
  transform: translate(var(--transLeft), var(--transTop));
}
.nowrap {
  white-space: nowrap;
}
.punctuation {
}
#attribution {
  font-family: helvetica;
  line-height: normal;
  opacity: 0;
  pointer-events: none;
  transition: opacity 700ms ease-in-out;
}
#attribution.completed {
  opacity: 1;
  pointer-events: auto;
}
